import gsap from "gsap";

const pointer = document.getElementById('pointer');

let pos = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
let mouse = { x: pos.x, y: pos.y };
let speed = 1;

let xSet = gsap.quickSetter(pointer, "x", "px");
let ySet = gsap.quickSetter(pointer, "y", "px");

const video = document.querySelector('video');

const pointerToggle = gsap.timeline({ paused: true }).to(pointer, { duration: 0.25, autoAlpha: 0, scale: 0 });

export default function customPointer() {

  document.documentElement.classList.add('hidden-pointer');

  document.body.addEventListener('mouseenter', () => {
    pointerToggle.reverse();
  });

  document.body.addEventListener('mouseleave', () => {
    pointerToggle.play();
  });

  if (video) {
    video.addEventListener('mouseenter', () => {
      pointerToggle.play();
    });

    video.addEventListener('mouseleave', () => {
      pointerToggle.reverse();
    });
  }


  window.addEventListener("mousemove", function (e) {
    mouse.x = e.x;
    mouse.y = e.y;
  });

  // Change cursor position on the fly
  gsap.ticker.add(() => {
    pos.x += (mouse.x - pos.x) * speed;
    pos.y += (mouse.y - pos.y) * speed;
    xSet(pos.x);
    ySet(pos.y);
  });

  pointerAim();

  function pointerAim() {

    const aimTop = document.getElementById('aimTop');
    const aimRight = document.getElementById('aimRight');
    const aimLeft = document.getElementById('aimLeft');
    const aimCenter = pointer.getElementsByClassName('pointer-center')[0];
    const triggers = document.querySelectorAll('a, .showreel, .close-popup, .slides-nav__button');

    const aimGrow = gsap.timeline({ paused: true });
    const aimPulse = gsap.timeline({ paused: true, yoyo: true, repeat: -1 });

    aimGrow
      .to(aimCenter, { duration: 0.2, autoAlpha: 0.5 }, "aim")
      .to(aimTop, { duration: 0.2, yPercent: -90 }, "aim")
      .to(aimLeft, { duration: 0.2, xPercent: -75, yPercent: 75 }, "aim")
      .to(aimRight, { duration: 0.2, xPercent: 75, yPercent: 75 }, "aim");

    aimPulse
      .to([aimTop, aimLeft, aimRight], { duration: 0.2, autoAlpha: 0.3 }, "aim");

    [...triggers].forEach(a => {

      a.addEventListener('mouseenter', () => {
        aimGrow.play();
        aimPulse.play();
      });

      a.addEventListener('mouseleave', () => {
        aimGrow.reverse();
        aimPulse.play(0);
        aimPulse.pause();
      });

    });
  }

}
