import Swiper, { EffectFade} from 'swiper/core';
Swiper.use([EffectFade]);

import { gsap } from "gsap";

export default function gallery() {

  const gallery = document.getElementById('gallery');
  if(!gallery) return;
  const leftSliderWrapper = gallery.getElementsByClassName('dark-slider')[0];
  const rightSliderWrapper = gallery.getElementsByClassName('right-slider')[0];

  const nextSlide = gallery.getElementsByClassName('next-slide')[0];
  const prevSlide = gallery.getElementsByClassName('prev-slide')[0];

  const slideInfo = gallery.getElementsByClassName('title-block')[0];
  const slideTitle = slideInfo.querySelector('h2');
  const slideText = slideInfo.querySelector('p');

  // Create right slider
  const rightSlider = new Swiper(rightSliderWrapper, {
    slidesPerView: 1,
    loop: true,
  });

  // Create left slider
  const leftSlider = new Swiper(leftSliderWrapper, {
    slidesPerView: 1,
    initialSlide: rightSlider.slides.length - 3,
    loop: true,
    effect: 'fade',
    speed: 1000
  });

  // Slider navigation
  const showNextSlide = () => {
    rightSlider.slideNext();
    leftSlider.slideNext();
  }

  const showPrevSlide = () => {
    rightSlider.slidePrev();
    leftSlider.slidePrev();
  }

  nextSlide.addEventListener('click', showNextSlide);
  prevSlide.addEventListener('click', showPrevSlide);

  // Change text on slide change
  rightSlider.on('slideChange', () => {

    let currentSlide = rightSlider.slides[rightSlider.realIndex + 1];

    leftSlider.slideTo(rightSlider.realIndex);

    gsap.timeline()
      .to([slideTitle, slideText], {
        duration: 0.2,
        autoAlpha: 0,
        y: 20,
        stagger: {
          from: "end",
          each: 0.15
        }
      }, "swipe")
      .call(() => {
        slideTitle.innerHTML = currentSlide.getAttribute('data-title');
        slideText.innerHTML = currentSlide.getAttribute('data-text');
      }, null, "swipe+=0.5")
      .to([slideTitle, slideText], {
        duration: 0.2,
        autoAlpha: 1,
        y: 0,
        stagger: {
          from: "start",
          each: 0.15
        }
      }, "swipe+=0.6");

  })

}