import { gsap } from "gsap";

export default function introParallax() {

  let intro = document.getElementById('intro');

  if(!intro) return;
  let parallaxItems = [...intro.querySelectorAll('[parallax]')];
  let fixer = -0.05;
  let centerX = intro.offsetWidth / 2;
  let centerY = intro.offsetHeight / 2;
  let pageX, pageY;

  // Background parallax
  intro.addEventListener('mousemove', bgParallax);

  function bgParallax(e) {
    pageX = (e.pageX - centerX) * fixer;
    pageY = (e.pageY - centerY) * fixer;

    parallaxItems.forEach(item => {

      let speedX = item.getAttribute('data-x');
      let speedY = item.getAttribute('data-y');
      let scaleData = item.getAttribute('data-size');

      // console.log(1 / pageY);

      // let scale = (scaleData) ? (1 * pageY) - 5 : 1;
      gsap.to(item, {
        x: (pageX * speedX),
        y: (pageY * speedY),
        // scale: scale,
        ease: "power1.easeOut"
      });
    });
  }
}